.product-list {
  display: flex;
  flex-flow: row wrap;
  gap: 50px 0;
  justify-content: space-between;
  margin-bottom: 50px;
  margin-top: 50px;
}

.item-product{
  text-decoration: none;
  color: white;
}
.input{
  background-color: aliceblue;
}
.cart{
  background-color: rgb(42, 141, 50);
  display: flex;
  color: rgb(68, 28, 28);
  border-radius: 10px;
  margin-top: 5px;
  margin-left: 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item-product-p:hover{
  border-radius: 10px;
  background-color: rgba(118, 119, 119, 0.418);
  color: white;
}

.product {
  background-color: rgba(23, 161, 35, 0.658);
  align-items: center;
  border-radius: 10px;
  border: solid gray 1px;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  height: 350px;
  justify-content: center;
  padding: 10px;
  width: 275px;
}
.product:hover{
  border: 1.5px rgb(148, 142, 142) solid;
}
.product img {
  width: 150px;
  border-radius: 10px;
}
