.container {
  display: flex;
  justify-content: space-between;
  background-color: rgba(23, 161, 35, 0.658);
}

.container-detalhe {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  margin: 5vh;
  min-width: 150px;
  overflow-y: scroll;
  padding: 0 10px;
  width: 70vh;
}

.container-menu{
  background-color: rgba(26, 29, 26, 0.404);
  border-radius: 15px;
  text-align: center;
  position: fixed;
  width: 100vw;
  max-height: 100vh;
  top: 50px;
  left: 75%;
  right: 0;
  padding: 15px;
  transition: all 1s;
}

.menususpenso{
  display: none;
  right: 1000px;
  transition: all 1s;
}

.label-menu{
  border: none;
  background-color: rgba(240, 248, 255, 0);
  color: rgb(49, 22, 22);
  padding: 15px 45px;
}
.menususpenso:hover {
  background-color: rgba(23, 161, 34, 0.705);
  border-radius: 10px;
  color: rgb(126, 123, 123);
  padding: 15px 45px;
  text-decoration: none;
}

.container-detalhe-1 {
  display: flex;
  color: rgb(78, 50, 13);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50vh;
  margin: 5vh;
  padding: 0 10px;
  width: 70vh;
}

.container-info {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40%;
}

.score{
  margin: 10px;
}
.header {
  background-color: rgba(23, 161, 34, 0.705);
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: space-around;
  width: 100vw;
}

.cart-ancor {
  color: rgb(49, 22, 22);
  padding: 15px 45px;
  text-decoration: none;
}

.cart-ancor:hover {
  background-color: rgba(23, 161, 34, 0.705);
  border-radius: 10px;
  color: rgb(126, 123, 123);
  padding: 15px 45px;
  text-decoration: none;
}

.foto {
  margin: 15px;
  width: 150px;
}

.comentarios {
  background-color: rgb(227, 227, 227);
  border: 1px solid;
  text-align: center;
}

.comentarios-rate {
  display: flex;
  gap: 35px;
  justify-content: space-around;
}

textarea {
  height: 35px;
}
