main {
  display: flex;
  flex-direction: row;
  width: 100%;
}

aside {
  background-color: rgba(23, 161, 35, 0.986);
  display: flex;
  flex-direction: column;
  height: 94.5vh;
  margin: 5vh;
  margin-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  min-width: 150px;
  overflow-y: scroll;
  padding: 0 10px;
  width: 500px;
}

.button-categories {
  font-size: medium;
  color: rgb(49, 22, 22);
  background-color: rgba(23, 161, 34, 0.705);
  border: none;
  padding: 10px;
}
.button-categories:hover {
  background-color: rgba(205, 214, 206, 0.705);
  border-radius: 10px;
}
.button{
  text-align: center;
  color: white;
  background-color: rgba(23, 161, 34, 0.705);
  border-radius: 10px;
}

.button:hover{
  border: 1px black solid;
}
.quantity-calc{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.gap{
  padding: 10px;
}

.h3{
  text-align: center;
  width: 250px;
}

.container-cart{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.enviar{
  padding: 10px;
}
.home-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 50px;
  width: 80%;
}
